.closeButton__content {
    position: relative;
    border: solid white 0px;
    display: block;
    width: 30px;
}

.closeButton__content::before {
    content: '';
    width: 32px;
    border: solid white 2px;
    position: absolute;
    top: -2px;
    left: -4px;
    transform: rotate(135deg);
}

.closeButton__content::after {
    content: '';
    width: 32px;
    border: solid white 2px;
    position: absolute;
    bottom: -2px;
    left: -4px;
    transform: rotate(-135deg);
}
