.gameSlider {
    width: 100%;
    display: flex;
    align-items: center;
}

.gameSlider__wrapper {
    width: 100%;
    display: flex;
    gap: 10px;
    height: 100%;
    overflow: hidden;
}

.gameSlider__item {
    transition: 300ms;
}

.gameSlider__image {
    height: 100%;
}

.gameSlider__message {
}

.gameSlider__popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: linear-gradient(
        180deg,
        rgba(6, 6, 6, 0.5),
        rgba(6, 6, 6, 0.7),
        rgba(6, 6, 6, 1)
    );
    backdrop-filter: blur(10px);
    display: flex;
    justify-content: center;
    align-items: center;
}

.gameSlider__image_fullscreen {
    transition: 300ms;
    width: 80%;
}

.gameSlider__closeButton {
    color: white;
    border: none;
    background: none;
    position: fixed;
    top: 20px;
    right: 20px;
    width: 50px;
    height: 50px;
}

@media only screen and (max-width: 800px) {
    .gameSlider__image_fullscreen {
        transition: 300ms;
        width: 100%;
    }

    .gameSlider__wrapper {
        overflow-x: scroll;
    }

    .gameSlider__controlls {
        display: none !important;
    }
}
